import * as THREE from 'three';

import { BUTTON_STATE, SwitchComponent } from '../components/SwitchComponent';

import { InteractionSystem } from '../utils/ECSYUtils';

const TOUCH_RADIUS = 0.01;
const BUTTON_SURFACE_Y = 0.061789;
const BUTTON_BOUNCE_BACK_SPEED = 0.4;
const BUTTON_MAX_Y = 0;
const BUTTON_MIN_Y = -0.02;
const BUTTON_LOCK_TIME = 0.1;

export class SwitchSystem extends InteractionSystem {
	init() {
		this._helperVec3 = new THREE.Vector3();
	}

	onExecute(delta, _time) {
		this.queries.switch.results.forEach((entity) => {
			let switchComponent = entity.getMutableComponent(SwitchComponent);
			let buttonMesh = switchComponent.buttonMesh;
			switchComponent.valueJustChanged = false;

			if (switchComponent.lockTimer > 0) {
				switchComponent.lockTimer -= delta;
			} else {
				let [intersect, deltaY] = this.checkButtonIntersect(buttonMesh);
				if (intersect) {
					buttonMesh.position.y += deltaY;
					if (buttonMesh.position.y <= BUTTON_MIN_Y) {
						buttonMesh.position.y = BUTTON_MIN_Y;
						if (switchComponent.state != BUTTON_STATE.FULLY_PRESSED) {
							// do fully pressed action
							switchComponent.lockTimer = BUTTON_LOCK_TIME;
							switchComponent.state = BUTTON_STATE.FULLY_PRESSED;
							switchComponent.value = !switchComponent.value;
							switchComponent.valueJustChanged = true; //
						}
					} else {
						switchComponent.state = BUTTON_STATE.PRESSED;
					}
				} else {
					buttonMesh.position.y += delta * BUTTON_BOUNCE_BACK_SPEED;
					if (buttonMesh.position.y >= BUTTON_MAX_Y) {
						buttonMesh.position.y = BUTTON_MAX_Y;
						if (switchComponent.state != BUTTON_STATE.IDLE) {
							// do fully recovered action
							switchComponent.lockTimer = BUTTON_LOCK_TIME;
							switchComponent.state = BUTTON_STATE.IDLE;
						}
					} else {
						switchComponent.state = BUTTON_STATE.PRESSED;
					}
				}
			}
		});
	}

	checkButtonIntersect(buttonMesh) {
		let intersect = false;
		let deltaY = 0;
		Object.values(this.handComponents).forEach((handComponent) => {
			if (!buttonMesh.geometry.boundsTree) {
				buttonMesh.geometry.computeBoundsTree();
			}

			const transformMatrix = new THREE.Matrix4()
				.copy(buttonMesh.matrixWorld)
				.invert()
				.multiply(handComponent.indexTip.matrixWorld);

			const sphere = new THREE.Sphere(undefined, TOUCH_RADIUS);
			sphere.applyMatrix4(transformMatrix);

			if (buttonMesh.geometry.boundsTree.intersectsSphere(sphere)) {
				handComponent.indexTip.getWorldPosition(this._helperVec3);
				buttonMesh.worldToLocal(this._helperVec3);
				intersect = true;
				deltaY = Math.min(
					Math.min(this._helperVec3.y - BUTTON_SURFACE_Y - TOUCH_RADIUS, 0),
					deltaY,
				);
			}
		});

		return [intersect, deltaY];
	}
}

SwitchSystem.addQueries({
	switch: { components: [SwitchComponent] },
});
