import * as THREE from 'three';

export class ExpandingParticleCloud extends THREE.Points {
	constructor(numParticles, baseVelocity, timeToLive = 1) {
		let geometry = new THREE.BufferGeometry();
		let particles = [];
		let vertices = new Float32Array(numParticles * 3);
		for (let i = 0; i < numParticles; i++) {
			const particle = {
				position: new THREE.Vector3(),
				direction: new THREE.Vector3(
					Math.random() - 0.5,
					Math.random() - 0.5,
					Math.random() - 0.5,
				).normalize(),
				speed: Math.random() + 1,
				acceleration: -Math.random(),
			};
			particles.push(particle);
			vertices[i * 3] = particle.position.x;
			vertices[i * 3 + 1] = particle.position.y;
			vertices[i * 3 + 2] = particle.position.z;
		}

		let verticesAttribute = new THREE.BufferAttribute(vertices, 3);
		geometry.setAttribute('position', verticesAttribute);
		let material = new THREE.PointsMaterial({ color: 0xffffff, size: 0.1 });

		super(geometry, material);

		this.particles = particles;
		this.verticesAttribute = verticesAttribute;

		this._helperVec3 = new THREE.Vector3();
		this.clock = new THREE.Clock();
		this.timeToLive = timeToLive;
		this.baseVelocity = baseVelocity;
		setTimeout(() => {
			this.clock = null;
			this.geometry.dispose();
			this.material.dispose();
			this.parent.remove(this);
		}, timeToLive * 1000);
	}

	updateMatrixWorld(force) {
		super.updateMatrixWorld(force);
		let delta = this.clock.getDelta();
		let deltaPosition = this.baseVelocity.clone().multiplyScalar(delta);
		for (let i = 0; i < this.particles.length; i++) {
			this.particles[i].position
				.add(
					this._helperVec3
						.copy(this.particles[i].direction)
						.multiplyScalar(delta * this.particles[i].speed),
				)
				.add(deltaPosition);
			this.particles[i].speed += this.particles[i].acceleration * delta;
			this.verticesAttribute.array[i * 3] = this.particles[i].position.x;
			this.verticesAttribute.array[i * 3 + 1] = this.particles[i].position.y;
			this.verticesAttribute.array[i * 3 + 2] = this.particles[i].position.z;
		}
		this.verticesAttribute.needsUpdate = true;

		this.material.size =
			(0.1 * (this.timeToLive - this.clock.elapsedTime)) / this.timeToLive;
		this.material.needsUpdate = true;
	}
}
