import * as THREE from 'three';

import {
	AstroidBlasterGameComponent,
	AstroidComponent,
	LaserComponent,
} from '../components/AstroidBlasterGameComponent';
import { deleteEntity, getOnlyEntity } from '../utils/ECSYUtils';

import { ExpandingParticleCloud } from '../lib/ExpandingParticleCloud';
import { ExpandingRing } from '../lib/ExpandingRing';
import { GameStateComponent } from '../components/GameStateComponent';
import { Object3DComponent } from '../components/Object3DComponent';
import { RigidBodyComponent } from '../components/RigidBodyComponent';
import { System } from 'ecsy';

const ASTROID_SPAWN_TIME = 1.5;

export class AstroidSystem extends System {
	init() {
		this.raycaster = new THREE.Raycaster();
		this.raycaster.far = 0.1;
	}

	execute(_delta, _time) {
		let scene = getOnlyEntity(this.queries.gameManager, false)?.getComponent(
			GameStateComponent,
		).scene;
		let game = getOnlyEntity(
			this.queries.astroidBlasterGame,
			false,
		)?.getMutableComponent(AstroidBlasterGameComponent);
		if (!game || !scene) return;

		if (game.gameActive) {
			if (
				game.astroidSpawnTimer >=
				ASTROID_SPAWN_TIME / game.astroidSpawnMultiplier
			) {
				this.spawnAstroid(game, scene);
				game.astroidSpawnTimer = 0;
			}
			this.updateAstroids(game, scene);
		} else {
			this.queries.astroid.results.forEach(deleteEntity);
		}
	}

	spawnAstroid(game, scene) {
		// console.log('spawning astroid');
		let astroidPrototypes = game.astroidPrototypes;
		let astroid = astroidPrototypes[
			Math.floor(astroidPrototypes.length * Math.random())
		].clone();
		scene.add(astroid);
		astroid.position.copy(game.astroidStartPlane.position);
		astroid.position.x += Math.random() * 2 - 1;
		astroid.position.y += Math.random() * 2 - 1;
		let astroidEntity = this.world.createEntity();
		astroidEntity.addComponent(RigidBodyComponent, {
			active: true,
			direction: game.astroidDirection.clone(),
			speed: (0.5 + Math.random() * 1.5) * game.astroidSpeedMultiplier,
			hasRotation: true,
			rotationAxis: new THREE.Vector3().randomDirection(),
			rotationSpeed: Math.random() * 3,
			perishable: false,
		});
		astroidEntity.addComponent(Object3DComponent, { value: astroid });
		astroidEntity.addComponent(AstroidComponent, {
			startPoint: astroid.position.clone(),
		});
	}

	updateAstroids(game, scene) {
		let astroidObjects = [];
		let entitiesToDelete = new Set();
		this.queries.astroid.results.forEach((entity) => {
			let astroidComponent = entity.getMutableComponent(AstroidComponent);
			let astroidObject = entity.getComponent(Object3DComponent).value;
			astroidObject.entity = entity;
			astroidObjects.push(astroidObject);
			if (
				astroidObject.position.distanceTo(astroidComponent.startPoint) >
				game.astroidMaxDistance
			) {
				game.playerHealthPoints -= 1;
				entitiesToDelete.add(entity);
				let expandingRing = new ExpandingRing({ color: 0xef6666 });
				scene.add(expandingRing);
				expandingRing.position.copy(astroidObject.position);
			}
		});
		this.queries.laser.results.forEach((entity) => {
			let laserObject = entity.getComponent(Object3DComponent).value;
			let laserRigidBody = entity.getMutableComponent(RigidBodyComponent);
			this.raycaster.set(laserObject.position, laserRigidBody.direction);
			let intersect = this.raycaster.intersectObjects(astroidObjects)[0];
			if (intersect) {
				let astroidEntity = intersect.object.entity;
				let astroidRigidBody = astroidEntity.getComponent(RigidBodyComponent);
				game.playerScore += 1;
				entitiesToDelete.add(astroidEntity);
				entitiesToDelete.add(entity);
				let particleCloud = new ExpandingParticleCloud(
					20,
					astroidRigidBody.direction
						.clone()
						.multiplyScalar(astroidRigidBody.speed),
				);
				scene.add(particleCloud);
				particleCloud.position.copy(intersect.object.position);
			}
		});
		entitiesToDelete.forEach(deleteEntity);
	}
}

AstroidSystem.queries = {
	gameManager: { components: [GameStateComponent] },
	astroidBlasterGame: { components: [AstroidBlasterGameComponent] },
	astroid: {
		components: [RigidBodyComponent, Object3DComponent, AstroidComponent],
	},
	laser: {
		components: [RigidBodyComponent, Object3DComponent, LaserComponent],
	},
};
