import { Component, Types } from 'ecsy';

import { ASTROID_BLASTER_CONSTANTS as ABC } from '../Constants';

export class AstroidBlasterGameComponent extends Component {
	resetGame() {
		this.gameActive = false;
		this.gameTimer = 0;
		this.astroidSpawnTimer = 0;
		this.playerHealthPoints = ABC.PLAYER_HEALTH_POINTS;
		this.playerScore = 0;
		this.astroidSpeedMultiplier = ABC.ASTROID_SPEED_MULTIPLIER;
		this.astroidSpawnMultiplier = ABC.ASTROID_SPAWN_MULTIPLIER;
	}
}

AstroidBlasterGameComponent.schema = {
	turretBase: { type: Types.Ref, default: undefined },
	turretBody: { type: Types.Ref, default: undefined },
	turretHandle: { type: Types.Ref, default: undefined },
	turretMuzzle: { type: Types.Ref, default: undefined },
	astroidPrototypes: { type: Types.Ref, default: undefined },

	astroidStartPlane: { type: Types.Ref, default: undefined },
	astroidDirection: { type: Types.Ref, default: undefined },
	astroidMaxDistance: { type: Types.Number, default: ABC.SHIELD_DISTANCE },

	astroidSpeedMultiplier: {
		type: Types.Number,
		default: ABC.ASTROID_SPEED_MULTIPLIER,
	},
	astroidSpawnMultiplier: {
		type: Types.Number,
		default: ABC.ASTROID_SPAWN_MULTIPLIER,
	},

	gameActive: { type: Types.Boolean, default: false },
	turretEngaged: { type: Types.Boolean, default: false },
	gameTimer: { type: Types.Number, default: 0 },
	astroidSpawnTimer: { type: Types.Number, default: 0 },
	playerHealthPoints: { type: Types.Number, default: ABC.PLAYER_HEALTH_POINTS },
	playerScore: { type: Types.Number, default: 0 },
	gameRecords: { type: Types.Ref, default: [] },
};

export class AstroidComponent extends Component {}

AstroidComponent.schema = {
	startPoint: { type: Types.Ref, default: undefined },
	healthPoints: { type: Types.Number, default: 2 },
};

export class LaserComponent extends Component {}

LaserComponent.schema = {
	numHits: { type: Types.Number, default: 2 },
};
