import * as THREE from 'three';

import { Text } from 'troika-three-text';

const CHARACTER_WIDTH = 0.22 / 12 / 0.03;
const LINE_HEIGHT = 0.043 / 0.03;

export class TextPanel extends THREE.Object3D {
	constructor(props = {}) {
		super();
		this.padding = props.padding ?? 0;
		this.cornerRadius = props.cornerRadius ?? 0;
		this.backgroundColor = props.backgroundColor ?? 0x808080;
		this.backgroundOpacity = props.backgroundOpacity ?? 0.5;
		this.textBlock = new Text();
		this.textBlock.font = 'assets/fonts/SpaceMono-Regular.ttf';
		this.textBlock.text = props.text ?? '';
		this.textBlock.fontSize = props.fontSize ?? 0.03;
		this.textBlock.color = props.fontColor ?? 0xffffff;
		this.textBlock.fillOpacity = props.fontOpacity ?? 1;
		this.textBlock.anchorX = '50%';
		this.textBlock.anchorY = '50%';
		this.textBlock.position.z = props.zOffset ?? 0;
		this.textBlock.sync();
		this.backgroundPanel = generateRoundedRectPlane(
			this.textBlock,
			this.padding,
			this.cornerRadius,
		);
		this.backgroundPanel.material.color.setHex(this.backgroundColor);
		this.backgroundPanel.material.opacity = this.backgroundOpacity;

		this.add(this.textBlock);
		this.add(this.backgroundPanel);
	}

	setText(text, updateBackground = false) {
		this.textBlock.text = text;
		if (updateBackground) {
			this.backgroundPanel.material.dispose();
			this.backgroundPanel.geometry.dispose();
			this.remove(this.backgroundPanel);
			this.backgroundPanel = generateRoundedRectPlane(
				this.textBlock,
				this.padding,
				this.cornerRadius,
			);
			this.backgroundPanel.material.color.setHex(this.backgroundColor);
			this.backgroundPanel.material.opacity = this.backgroundOpacity;
			this.add(this.backgroundPanel);
		}
		this.textBlock.sync();
	}

	dispose() {
		this.textBlock.dispose();
		this.backgroundPanel.material.dispose();
		this.backgroundPanel.geometry.dispose();
		this.remove(this.textBlock);
		this.remove(this.backgroundPanel);
		this.parent?.remove(this);
	}
}

export const generateRoundedRectPlane = (textBlock, padding, radius) => {
	let lines = textBlock.text.split(/\r?\n/);
	let numLines = lines.length;
	let lineLength = 0;
	lines.forEach((line) => {
		lineLength = Math.max(line.length, lineLength);
	});
	let width = lineLength * CHARACTER_WIDTH * textBlock.fontSize + padding;
	let height = numLines * LINE_HEIGHT * textBlock.fontSize + padding;
	const ctx = new THREE.Shape();
	let x = -width / 2;
	let y = -height / 2;
	ctx.moveTo(x, y + radius);
	ctx.lineTo(x, y + height - radius);
	ctx.quadraticCurveTo(x, y + height, x + radius, y + height);
	ctx.lineTo(x + width - radius, y + height);
	ctx.quadraticCurveTo(x + width, y + height, x + width, y + height - radius);
	ctx.lineTo(x + width, y + radius);
	ctx.quadraticCurveTo(x + width, y, x + width - radius, y);
	ctx.lineTo(x + radius, y);
	ctx.quadraticCurveTo(x, y, x, y + radius);

	const geometry = new THREE.ShapeGeometry(ctx);
	const material = new THREE.MeshBasicMaterial({
		color: 0x00ff00,
		transparent: true,
	});
	const mesh = new THREE.Mesh(geometry, material);
	return mesh;
};
