import * as THREE from 'three';

import {
	Collider,
	ObjectCollider,
	VerticalCollider,
} from '../components/TagComponents';
import {
	LeftController,
	RightController,
	VrControllerComponent,
} from '../components/VrControllerComponent';
import {
	RigidBodyComponent,
	createDefaultRigidBodySchema,
} from '../components/RigidBodyComponent';
import {
	SWITCH_TAG_MAPPING,
	SwitchComponent,
} from '../components/SwitchComponent';

import { ControllerInterface } from '../lib/ControllerInterface';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { GameStateComponent } from '../components/GameStateComponent';
import { GrabbableComponent } from '../components/GrabbableComponent';
import { HandComponent } from '../components/HandComponent';
import { Object3DComponent } from '../components/Object3DComponent';
// import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader.js';
// import { RoughnessMipmapper } from 'three/examples/jsm/utils/RoughnessMipmapper.js';
import { System } from 'ecsy';
import { XRControllerModelFactory } from 'three/examples/jsm/webxr/XRControllerModelFactory';
import { processModel } from '../utils/modelProcessingUtils';

const ENV_MODEL_PATH = 'assets/space-voyager-simulator.glb';

export class SceneCreationSystem extends System {
	execute(_delta, _time) {
		this.queries.gameManager.added.forEach((entity) => {
			let gameStateComponent = entity.getComponent(GameStateComponent);
			let scene = gameStateComponent.scene;
			let renderer = gameStateComponent.renderer;
			let viewerTransform = gameStateComponent.viewerTransform;

			createLighting(scene);
			createEnvironment(scene, this.world);
			setupControllers(renderer, viewerTransform, this.world);
		});
	}
}

SceneCreationSystem.queries = {
	gameManager: { components: [GameStateComponent], listen: { added: true } },
};

/**
 * create lighting for scene
 * @param {THREE.Scene} scene
 */
const createLighting = (scene) => {
	let ambientLight = new THREE.AmbientLight(0xffffff, 0.2);
	scene.add(ambientLight);

	let directionalLight = new THREE.DirectionalLight(0xffffff, 0.2);
	scene.add(directionalLight);
};

/**
 * Load and process model, setup game objects
 * @param {THREE.Scene} scene
 * @param {ecsy.World} world
 */
const createEnvironment = (scene, world) => {
	let loader = new GLTFLoader();

	loader.load(ENV_MODEL_PATH, function (gltf) {
		let object = gltf.scene;
		let colliderObjects = [];
		let grabbableObjects = [];
		let switchObjects = [];

		let environmentGroup = processModel(
			object,
			colliderObjects,
			grabbableObjects,
			switchObjects,
		);

		loadColliders(colliderObjects, world, scene);

		loadGrabbables(grabbableObjects, world, scene);

		loadSwitches(switchObjects, world, scene);

		scene.add(environmentGroup);
	});
};

const loadColliders = (objects, world, scene) => {
	objects.forEach((object) => {
		let entity = world.createEntity();
		entity.addComponent(Collider);
		entity.addComponent(Object3DComponent, { value: object });
		if (object.userData.verticalCollider) {
			entity.addComponent(VerticalCollider);
		} else if (object.userData.objectCollider) {
			entity.addComponent(ObjectCollider);
		}
		object.visible = false;
		scene.add(object);
	});
};

const loadGrabbables = (objects, world, scene) => {
	objects.forEach((object) => {
		let entity = world.createEntity();
		entity.addComponent(GrabbableComponent);
		entity.addComponent(RigidBodyComponent, createDefaultRigidBodySchema());
		entity.addComponent(Object3DComponent, { value: object });

		scene.add(object);
	});
};

const loadSwitches = (objects, world, scene) => {
	let switches = {};
	objects.forEach((object) => {
		let switchName = object.name.split('_')[2];
		if (!switches[switchName]) {
			switches[switchName] = {
				base: null,
				button: null,
			};
		}
		if (object.name.split('_')[1] == 'base') {
			switches[switchName].base = object;
		} else if (object.name.split('_')[1] == 'button') {
			switches[switchName].button = object;
		}
	});
	Object.entries(switches).forEach((entry) => {
		let [switchName, switchData] = entry;
		let switchEntity = world.createEntity();
		switchEntity.addComponent(SwitchComponent, {
			baseMesh: switchData.base,
			buttonMesh: switchData.button,
		});
		switchEntity.addComponent(SWITCH_TAG_MAPPING[switchName]);
		scene.add(switchData.base);
		switchData.base.attach(switchData.button);
	});
};

/**
 * Setup controllers and create wrappers for them.
 * @param {THREE.WebGLRenderer} renderer - webgl renderer for the scene
 * @param {THREE.Object3D} viewerTransform - position of the player
 * @param {ecsy.World} world - the ECSY world
 */
const setupControllers = (renderer, viewerTransform, world) => {
	const controllerModelFactory = new XRControllerModelFactory();

	for (let i = 0; i < 2; i++) {
		let controller = renderer.xr.getController(i);
		viewerTransform.add(controller);
		controller.addEventListener('connected', function (event) {
			let handedness = event.data.handedness;
			let controllerGrip = renderer.xr.getControllerGrip(i);
			let controllerModel = controllerModelFactory.createControllerModel(
				controllerGrip,
			);
			controllerGrip.add(controllerModel);
			viewerTransform.add(controllerGrip);

			let controllerEntity = world.createEntity();
			let controllerInterface = new ControllerInterface(
				handedness,
				this,
				event.data.gamepad,
				controllerModel,
			);
			controllerEntity.addComponent(VrControllerComponent, {
				handedness,
				controllerInterface,
			});
			controllerEntity.addComponent(
				handedness === 'left' ? LeftController : RightController,
			);

			setupHands(handedness, controllerModel, controllerGrip, controllerEntity);
		});
	}
};

const setupHands = (
	handedness,
	controllerModel,
	controllerGrip,
	controllerEntity,
) => {
	let loader = new GLTFLoader();

	loader.load('assets/hand-' + handedness + '.glb', function (gltf) {
		let models = gltf.scene;

		let poses = {};
		let joints = {};
		let defaultModel = null;
		let indexTip = null;

		models.children.forEach((model) => {
			let jointQuaternions = {};
			let isDefaultModel = model.name == 'default';
			if (isDefaultModel) {
				defaultModel = model;
			}

			model.traverse((node) => {
				if (node.type == 'Bone') {
					let jointKey = node.name.split('_')[0];
					jointQuaternions[jointKey] = node.quaternion.clone();

					if (isDefaultModel) {
						joints[jointKey] = node;
						if (jointKey == 'index3') {
							indexTip = new THREE.Object3D();
							// indexTip = new THREE.Mesh(
							// 	new THREE.SphereBufferGeometry(0.01, 10, 10),
							// 	new THREE.MeshBasicMaterial({}),
							// );
							indexTip.position.set(0, 0.03, 0);
							node.add(indexTip);
						}
					}
				}
			});
			poses[model.name] = jointQuaternions;
		});

		controllerModel.visible = false;
		controllerGrip.add(defaultModel);
		let grabSpace = new THREE.Object3D();
		controllerGrip.add(grabSpace);
		let directionScalar = handedness == 'right' ? 1 : -1;
		grabSpace.position.set(directionScalar * -0.1, 0, 0);
		grabSpace.rotateY((Math.PI / 180) * directionScalar * 10);
		grabSpace.rotateX((Math.PI / 180) * -50);

		controllerEntity.addComponent(HandComponent, {
			joints: joints,
			poses: poses,
			model: defaultModel,
			grabSpace: grabSpace,
			indexTip: indexTip,
		});
	});
};
