import * as THREE from 'three';

import { ArcadeSwitch, SwitchComponent } from '../components/SwitchComponent';

import { ASTROID_BLASTER_CONSTANTS as ABC } from '../Constants';
import { AstroidBlasterGameComponent } from '../components/AstroidBlasterGameComponent';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { GameStateComponent } from '../components/GameStateComponent';
import { System } from 'ecsy';
import { TextPanel } from '../lib/TextPanel';
import { getOnlyEntity } from '../utils/ECSYUtils';

export class AstroidBlasterGameSystem extends System {
	init() {
		this.assetLoaded = false;
		this.turretUI = null;
		this.gameUI = null;
		this.aimRings = null;
	}

	execute(delta, _time) {
		if (!this.assetLoaded) {
			this.queries.gameManager.results.forEach((entity) => {
				this.loadGameAssets(entity.getComponent(GameStateComponent).scene);
				this.assetLoaded = true;
			});
		}

		let game = getOnlyEntity(
			this.queries.astroidBlasterGame,
			false,
		)?.getMutableComponent(AstroidBlasterGameComponent);

		if (!game) return;

		this.turretUI.visible = game.turretEngaged && game.gameActive;
		this.aimRings.visible = game.turretEngaged && game.gameActive;
		if (this.gameUI.visible == game.gameActive) {
			this.gameUI.setText(this.generateGameUIText(game), true);
		}
		this.gameUI.visible = !game.gameActive;

		this.queries.arcadeSwitch.results.forEach((entity) => {
			let switchComponent = entity.getComponent(SwitchComponent);
			if (switchComponent.valueJustChanged) {
				if (game.gameActive && game.gameTimer > 5) {
					game.resetGame();
				} else if (!game.gameActive) {
					game.gameActive = true;
				}
			}
		});

		if (game.gameActive) {
			this.executeGame(delta, game);
		}
	}

	loadGameAssets(scene) {
		let loader = new GLTFLoader();
		let turretBase, turretBody, turretHandle, turretMuzzle, astroidStartPlane;
		let astroidPrototypes = [];

		loader.load(ABC.GLTF_PATH, (gltf) => {
			let object = gltf.scene;
			object.children.forEach((node) => {
				switch (node.name) {
					case ABC.TURRET_BASE_NAME:
						turretBase = node;
						break;
					case ABC.TURRET_BODY_NAME:
						turretBody = node;
						break;
					case ABC.TURRET_HANDLE_NAME:
						turretHandle = node;
						break;
					case ABC.TURRET_MUZZLE_NAME:
						turretMuzzle = node;
						break;
					case ABC.ASTROID_START_PLANE:
						astroidStartPlane = node;
						break;
					default:
						if (node.name.startsWith(ABC.ASTROID_PREFIX)) {
							astroidPrototypes.push(node);
						}
						break;
				}
			});
			scene.attach(turretBase);
			turretBase.attach(turretBody);
			turretBody.attach(turretHandle);
			turretBody.attach(turretMuzzle);
			let entity = this.world.createEntity();
			entity.addComponent(AstroidBlasterGameComponent, {
				turretBase,
				turretBody,
				turretHandle,
				turretMuzzle,
				astroidPrototypes,
				astroidStartPlane,
				astroidDirection: new THREE.Vector3(0, 0, 1),
				astroidMaxDistance: 8,
			});
			this.turretUI = new TextPanel({
				text: this.generateTurretUIText(),
				fontOpacity: 0.5,
				backgroundOpacity: 0.3,
				padding: 0.01,
				cornerRadius: 0.02,
				zOffset: 0.005,
			});
			this.turretUI.rotateY(Math.PI);
			this.turretUI.rotateX(Math.PI / -6);
			this.turretUI.position.set(0, 0.06, -0.26);
			turretBody.add(this.turretUI);

			this.aimRings = new THREE.Group();
			const aimRingGeometry = new THREE.RingGeometry(0.03, 0.04, 16);
			for (let i = 0; i < 4; i++) {
				const ring = new THREE.Mesh(
					aimRingGeometry,
					new THREE.MeshBasicMaterial({
						color: 0xffffff,
						transparent: true,
						opacity: (0.5 * (4 - i)) / 4,
					}),
				);
				ring.position.z = 0.3 * (i + 1);
				ring.rotateY(Math.PI);
				this.aimRings.add(ring);
			}
			this.aimRings.position.copy(
				turretMuzzle.getWorldPosition(new THREE.Vector3()),
			);
			turretMuzzle.attach(this.aimRings);

			this.gameUI = new TextPanel({
				text: this.generateGameUIText(),
				fontSize: 0.1,
				fontOpacity: 0.5,
				backgroundOpacity: 0.3,
				padding: 0.01,
				cornerRadius: 0.02,
				zOffset: 0.005,
			});
			this.gameUI.rotateY(Math.PI);
			this.gameUI.position.copy(turretBase.position);
			this.gameUI.position.y += 0.5;
			this.gameUI.position.z += 1;
			scene.add(this.gameUI);
		});
	}

	generateTurretUIText(game = null) {
		return (
			'Health: ' +
			formatNumber(
				game
					? Math.floor(
							(game.playerHealthPoints / ABC.PLAYER_HEALTH_POINTS) * 100,
					  )
					: 100,
				3,
			) +
			'%\nScore:  ' +
			formatNumber(game ? game.playerScore : 0, 4) +
			'\nTimer:  ' +
			formatNumber(game ? Math.floor(game.gameTimer) : 0, 3) +
			's'
		);
	}

	generateGameUIText(game = null) {
		let displayText = '';
		if (!game || game.gameRecords.length == 0) {
			displayText = ABC.WELCOME_TEXT;
		} else {
			let gameRecord = game.gameRecords[game.gameRecords.length - 1];
			displayText = ABC.GAMEOVER_TEXT.replace(
				'<time>',
				Math.floor(gameRecord.timeSurvived),
			).replace('<score>', gameRecord.playerScore);
			// return (
			// 	'Game Over!!\n' +
			// 	'Player Score: ' +
			// 	formatNumber(game ? gameRecord.playerScore : 0, 4) +
			// 	'\nTime survived: ' +
			// 	formatNumber(game ? Math.floor(gameRecord.timeSurvived) : 0, 3)
			// );
		}
		return wrapText(displayText, 30, true);
	}

	executeGame(delta, game) {
		game.gameTimer += delta;
		game.astroidSpawnMultiplier = 1 + (game.gameTimer / 15) * 0.1;
		game.astroidSpeedMultiplier = 1 + (game.gameTimer / 15) * 0.1;
		game.astroidSpawnTimer += delta;
		this.turretUI.setText(this.generateTurretUIText(game));

		if (game.playerHealthPoints <= 0) {
			game.gameRecords.push({
				playerScore: game.playerScore,
				timeSurvived: game.gameTimer,
			});
			game.resetGame();
		}
	}
}

AstroidBlasterGameSystem.queries = {
	gameManager: { components: [GameStateComponent] },
	arcadeSwitch: { components: [SwitchComponent, ArcadeSwitch] },
	astroidBlasterGame: { components: [AstroidBlasterGameComponent] },
};

const formatNumber = (number, digits) => {
	let numberString = number.toString();
	while (numberString.length < digits) numberString = '0' + numberString;
	return numberString;
};

const wrapText = (text, maxLength, center = false) => {
	let lines = text.split(/\r?\n/);
	let lineLength = 0;
	lines = lines.flatMap((line) => {
		let sublines = [];
		let curretLine = '';
		let words = line.split(' ');
		words.forEach((word) => {
			if (curretLine.length + word.length + 1 > maxLength) {
				sublines.push(curretLine);
				curretLine = '';
			}
			if (curretLine.length == 0) {
				curretLine = word;
			} else {
				curretLine += ' ' + word;
			}
		});
		if (curretLine.length != 0) {
			sublines.push(curretLine);
		}
		return sublines;
	});
	lines.forEach((line) => {
		lineLength = Math.max(line.length, lineLength);
	});
	if (center) {
		lines = lines.map((line) => {
			let numSpaces = Math.floor((lineLength - line.length) / 2);
			return ' '.repeat(numSpaces) + line;
		});
	}

	return lines.join('\n');
};
