import * as THREE from 'three';

export class ExpandingRing extends THREE.Mesh {
	constructor(props = {}) {
		let geometry = new THREE.RingGeometry(
			props.innerRadius ?? 0.05,
			props.outerRadius ?? 0.15,
			props.thetaSegments ?? 16,
		);
		let material = new THREE.MeshBasicMaterial({
			color: props.color ?? 0xffffff,
			side: THREE.DoubleSide,
			transparent: true,
			opacity: props.opacity ?? 1,
		});
		super(geometry, material);
		this.timeToLive = props.timeToLive ?? 1;
		this.expandingSpeed = props.expandingSpeed ?? 2;
		this.startingOpacity = this.material.opacity;
		this.clock = new THREE.Clock();
		setTimeout(() => {
			this.clock = null;
			this.geometry.dispose();
			this.material.dispose();
			this.parent.remove(this);
		}, this.timeToLive * 1000);
	}

	updateMatrixWorld(force) {
		super.updateMatrixWorld(force);
		this.clock.getDelta();
		this.scale.setScalar(1 + this.expandingSpeed * this.clock.elapsedTime);
		this.material.opacity =
			(this.startingOpacity * (this.timeToLive - this.clock.elapsedTime)) /
			this.timeToLive;
		this.material.needsUpdate = true;
	}
}
