import { Component, TagComponent, Types } from 'ecsy';

export const BUTTON_STATE = {
	IDLE: 0,
	PRESSED: 1,
	FULLY_PRESSED: 2,
};

export class SwitchComponent extends Component {}

SwitchComponent.schema = {
	baseMesh: { type: Types.Ref, default: undefined },
	buttonMesh: { type: Types.Ref, default: undefined },
	state: { type: Types.Number, default: BUTTON_STATE.IDLE },
	value: { type: Types.Boolean, default: false },
	valueJustChanged: { type: Types.Boolean, default: false },
	lockTimer: { type: Types.Number, default: 0 },
};

export class ArcadeSwitch extends TagComponent {}

export const SWITCH_TAG_MAPPING = {
	arcade: ArcadeSwitch,
};
